<script>
export default {
  data() {
    return {};
  },
  computed: {
  },
  created() {
    document.addEventListener("DOMContentLoaded", function () {
      window.setTimeout(
        document.querySelector("svg").classList.add("animated"),
        1000
      );
    });
  },
};
